import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col, Checkbox } from 'antd';
import { TypeComponentPoint } from 'dump/data';

const urlForm = '#zones/form';
const columns = [
  {
    title: 'STT',
    dataIndex: 'stt',
    key: 'id',
  },
  {
    title: 'Ký hiệu',
    dataIndex: 'kh',
    key: 'label',
  },
  {
    title: 'Tên',
    dataIndex: 'ten',
    key: 'label',
  },
  {
    title: 'Tỷ lệ/Hệ số',
    dataIndex: 'tlhs',
    key: 'label',
  },
  {
    title: 'Tỷ lệ nhóm',
    dataIndex: 'tln',
    key: 'label',
  },
  {
    title: 'Nhóm thành phần',
    dataIndex: 'ntp',
    key: 'label',
  },
  {
    title: 'Chuyên cần',
    dataIndex: 'cc',
    key: 'label',
    render: (text, record) => <Checkbox checked={text} />,
  },
  {
    title: 'Chọn mặc định',
    dataIndex: 'cmd',
    key: 'label',
    render: (text, record) => <Checkbox checked={text} />,
  },
  {
    title: 'Thực hành',
    dataIndex: 'th',
    key: 'label',
    render: (text, record) => <Checkbox checked={text} />,
  },

  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) => (
      <Fragment>
        <BtnEdit href={`${urlForm}/edit/${record.id}`} />
        <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
          <BtnDelete />
        </Popconfirm>
      </Fragment>
    ),
  },
];

class ListTypeComponentPointPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Điểm thành phần">
        <Row>
          <Col span={24}>
            <BtnAddNew href={`${urlForm}/new`} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={TypeComponentPoint} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeComponentPointPage />
  </Page>
);



